@import url("https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Caveat&display=swap");

body {
  background-color: #f6f6f6;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Architects Daughter";
  min-height: 100vh;
  margin: 0px;
}

@media (min-width: 650px) {
  .App {
    width: 640px;
  }
}

@media (max-width: 650px) {
  .App {
    width: 90vw;
  }
}

#root {
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.App {
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
}

/* My Button */

.MyButton {
  cursor: pointer;
  border: none;
  border-radius: 5px;

  padding: 10px 20px 10px 20px;

  font-size: 18px;

  white-space: nowrap;
  font-family: "Architects Daughter";
}

.MyButton_default {
  background-color: #ececec;
  color: black;
}

.MyButton_positive {
  background-color: #64c964;
  color: white;
}

.MyButton_negative {
  background-color: #fd565f;
  color: white;
}

/* HEADER */

header {
  padding-top: 20px;
  padding-bottom: 20px;

  display: flex;
  align-items: center;
  border-bottom: 1px solid #e2e2e2;
}

header > div {
  display: flex;
}

header .head_text {
  width: 50%;
  font-size: 25px;
  justify-content: center;
}

header .head_btn_left {
  width: 25%;
  justify-content: start;
}

header .head_btn_right {
  width: 25%;
  justify-content: end;
}

header button {
  font-family: "Architects Daughter";
}

/* DiaryList */

.DiaryList .menu_wrapper {
  margin-top: 20px;
  margin-bottom: 30px;

  display: flex;
  justify-content: space-between;
}

.DiaryList .menu_wrapper .right_col {
  flex-grow: 1;
}

.DiaryList .menu_wrapper .right_col button {
  width: 100%;
}

.DiaryList .ControlMenu {
  margin-right: 10px;
  border: none;
  border-radius: 5px;
  background-color: #ececec;

  padding: 10px 20px 10px 20px;
  cursor: pointer;
  font-family: "Architects Daughter";
  font-size: 18px;
}

/* DiaryItem */

.DiaryItem {
  padding-top: 15px;
  padding-bottom: 15px;

  border-bottom: 1px solid #e2e2e2;
  display: flex;
  justify-content: space-between;
}

.DiaryItem .emotion_img_wrapper {
  cursor: pointer;
  min-width: 120px;
  height: 80px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}

.DiaryItem .emotion_img_wrapper img {
  width: 50%;
  height: 90%;
}

.DiaryItem .info_wrapper {
  margin-left: 20px;
  flex-grow: 1;
  cursor: pointer;
}

.DiaryItem .diary_date {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 5px;
}

.DiaryItem .diary_content_preview {
  font-size: 15px;
}

.DiaryItem .btn_wrapper {
  min-width: 70px;
}

/* DiaryEditor */

.DiaryEditor {
}

.DiaryEditor section {
  margin-bottom: 40px;
}

.DiaryEditor h4 {
  font-size: 22px;
  font-weight: bold;
}

.DiaryEditor .input_date {
  border: none;
  border-radius: 5px;
  background-color: #ececec;
  padding: 10px 20px 10px 20px;
  cursor: pointer;
  font-family: "Architects Daughter";
  font-size: 20px;
}

.DiaryEditor .emotion_list_wrapper {
  display: grid;
  grid-template-columns: repeat(5, auto);
  gap: 2%;
}

.DiaryEditor textarea {
  font-family: "Architects Daughter";
  font-size: 20px;

  box-sizing: border-box;
  width: 100%;
  min-height: 200px;
  resize: vertical;

  border: none;
  border-radius: 5px;
  background-color: #ececec;

  padding: 20px;
}

.DiaryEditor .control_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* EmotionItem */

.Emotion_item {
  cursor: pointer;

  border-radius: 5px;
  padding-top: 20px;
  padding-bottom: 20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Emotion_item img {
  width: 50%;
  margin-bottom: 10px;
}

.EmotionItem span {
  font-size: 18px;
}

.EmotionItem_off {
  background-color: white;
}

.EmotionItem_on_1 {
  background-color: #fd565f;
  color: white;
}

.EmotionItem_on_2 {
  background-color: #fd8446;
  color: white;
}

.EmotionItem_on_3 {
  background-color: #fdce17;
  color: white;
}

.EmotionItem_on_4 {
  color: white;
  background-color: #9dd772;
}

.EmotionItem_on_5 {
  color: white;
  background-color: #64c964;
}

/* Diary */

.DiaryPage {
}

.DiaryPage section {
  width: 100%;
  margin-bottom: 100px;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.DiaryPage h4 {
  font-size: 22px;
  font-weight: bold;
}

.DiaryPage .diary_img_wrapper {
  margin-top: 50px;
  width: 150px;
  height: 150px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.DiaryPage img {
  width: 150px;
  height: 150px;
}

.DiaryPage .diary_img_wrapper_1 {
  color: black;
}

.DiaryPage .diary_img_wrapper_2 {
  color: black;
}

.DiaryPage .diary_img_wrapper_3 {
  color: black;
}

.DiaryPage .diary_img_wrapper_4 {
  color: black;
}

.DiaryPage .diary_img_wrapper_5 {
  color: black;
}

.DiaryPage .emotion_descript_wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.DiaryPage .emotion_descript {
  margin-top: 25px;
  font-size: 20px;
  text-align: center;
  align-self: center;
}

.DiaryPage .emtion_descript_color_wrapper {
  padding-top: 15px;
  padding-left: 5px;
  font-size: 50px;
  width: 10%;
}

.DiaryPage .emotion_descript_color_1 {
  color: #fd565f;
}
.DiaryPage .emotion_descript_color_2 {
  color: #fd8446;
}
.DiaryPage .emotion_descript_color_3 {
  color: #fdce17;
}
.DiaryPage .emotion_descript_color_4 {
  color: #9dd772;
}
.DiaryPage .emotion_descript_color_5 {
  color: #64c964;
}

.DiaryPage .diary_content_wrapper {
  width: 100%;
  background-color: #ececec;
  border-radius: 5px;
  word-break: keep-all;
  overflow: break-word;
}

.DiaryPage .diary_content_wrapper p {
  padding: 20px;
  text-align: left;
  font-size: 20px;
  font-family: "Caveat";
  font-weight: 400;
  line-height: 2.5;
}
